<section>
  <h2>Wanneer zal de film te zien zijn?</h2>

  <p>Iedereen die wel eens iets met fotografie heeft gedaan zal het weten: dieren zijn niet voorspelbaar. Ook de licht- en weersomstandigheden willen nog wel eens variren. Daarom is het niet alleen moeilijk, maar vinden we het ook zonde om een harde deadline te stellen.</p>

  <p>We zijn al vier jaar bezig met filmen, dus we zijn al een eind op weg. We mikken op eind 2024.</p>
