<script lang="ts">
  import { onMount } from "svelte";
  import { Router, Route, Link } from "svelte-navigator";
  import Home from './routes/Home.svelte';
  import Sources from "./routes/Sources.svelte";

  $: innerWidth = 0;
  $: width = Math.min(
    Math.max(window.innerWidth, window.innerHeight) * 0.5,
    Math.max(innerWidth, 300)
  );

  $: videoUrl = getVideoUrl();
  onMount(onResize);

  function onResize() {
    const newVideoUrl = getVideoUrl();
    if (newVideoUrl == videoUrl) return;
    videoUrl = newVideoUrl;
  }

  function getVideoUrl() {
    const orientation =
      window.innerHeight > window.innerWidth ? "portrait" : "landscape";
    return `./assets/video/bg_${orientation}.mp4`;
  }
</script>

<Router>
  <main style="width:{width}px">
    <video preload="none" src={videoUrl} autoplay muted loop playsinline />
    <div class="content">
      <Link to="bronnen">Bronnen</Link>
      <Route path="/" component="{Home}" />
      <Route path="bronnen" component="{Sources}" />
    </div>
  </main>
</Router>
<svelte:window on:resize={onResize} bind:innerWidth />

<style>
  main {
    margin: auto;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: stretch;
    padding: 4rem;
    width: 100vw;
    justify-content: center;
  }

  .content {
    padding-top: 2rem;
    position: relative;
    display: flex;
    flex-direction: column;
    text-align: left;
    gap: 2rem;
  }

  video {
    position: fixed;
    inset: 0;
    width: 100vw;
    height: 100vh;
    object-fit: cover;
    opacity: 0.2;
    z-index: -10;
    filter: blur(1rem);
  }
</style>
